/**
 * Map View Component
 * Displays farm boundary and risk zones
 */

import React, { useEffect, useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

const MapView = ({ farmData, farms }) => {
  // If farms prop is provided, show all farms; otherwise, show single farmData
  const [userLocation, setUserLocation] = useState(null);
  const [mapCenter, setMapCenter] = useState(null);

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          setUserLocation({
            latitude: pos.coords.latitude,
            longitude: pos.coords.longitude,
          });
        },
        () => setUserLocation(null),
        { enableHighAccuracy: true, timeout: 10000 }
      );
    }
  }, []);

  useEffect(() => {
    if (userLocation) {
      setMapCenter([userLocation.latitude, userLocation.longitude]);
    } else if (farms && farms.length > 0) {
      setMapCenter([farms[0].location.latitude, farms[0].location.longitude]);
    } else if (farmData) {
      setMapCenter([farmData.location.latitude, farmData.location.longitude]);
    }
  }, [userLocation, farmData, farms]);

  return (
    <div className="bg-gray-50 p-4 md:p-6">
      {/* Interactive Map */}
      <div className="relative w-full h-96 rounded-lg overflow-hidden">
        {mapCenter && (
          <MapContainer
            center={mapCenter}
            zoom={16}
            scrollWheelZoom={false}
            style={{ height: '100%', width: '100%' }}
          >
            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {/* Show all farms if provided */}
            {farms && farms.length > 0
              ? farms.map((farm, idx) => (
                  <Marker
                    key={farm.farmId || idx}
                    position={[farm.location.latitude, farm.location.longitude]}
                    icon={L.icon({ iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png', iconSize: [25, 41], iconAnchor: [12, 41] })}
                  >
                    <Popup>
                      <div>
                        <strong>{farm.farmName || farm.id}</strong><br />
                        Lat: {farm.location.latitude.toFixed(5)}<br />
                        Lng: {farm.location.longitude.toFixed(5)}
                      </div>
                    </Popup>
                  </Marker>
                ))
              : farmData && (
                  <Marker
                    position={[farmData.location.latitude, farmData.location.longitude]}
                    icon={L.icon({ iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png', iconSize: [25, 41], iconAnchor: [12, 41] })}
                  >
                    <Popup>
                      <div>
                        <strong>{farmData.farmName}</strong><br />
                        Lat: {farmData.location.latitude.toFixed(5)}<br />
                        Lng: {farmData.location.longitude.toFixed(5)}
                      </div>
                    </Popup>
                  </Marker>
                )}
            {/* Show user location if available */}
            {userLocation && (
              <Marker
                position={[userLocation.latitude, userLocation.longitude]}
                icon={L.icon({ iconUrl: 'https://cdn-icons-png.flaticon.com/512/684/684908.png', iconSize: [32, 32], iconAnchor: [16, 32] })}
              >
                <Popup>
                  <div>
                    <strong>Your Location</strong><br />
                    Lat: {userLocation.latitude.toFixed(5)}<br />
                    Lng: {userLocation.longitude.toFixed(5)}
                  </div>
                </Popup>
              </Marker>
            )}
          </MapContainer>
        )}
      </div>

      {/* Map Legend */}
      <div className="mt-4 grid grid-cols-2 md:grid-cols-4 gap-2">
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-green-500 rounded"></div>
          <span className="text-sm text-gray-700">Low Risk</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-yellow-500 rounded"></div>
          <span className="text-sm text-gray-700">Moderate Risk</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-red-500 rounded"></div>
          <span className="text-sm text-gray-700">High Risk</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-red-900 rounded"></div>
          <span className="text-sm text-gray-700">Very High Risk</span>
        </div>
      </div>

      {/* Info Note */}
      <p className="text-xs text-gray-600 mt-4 p-3 bg-blue-50 rounded-lg">
        üí° Risk zones update automatically based on real-time sensor and satellite data. More detailed mapping coming soon.
      </p>
    </div>
  );
};

export default MapView;
